.header {
  display: flex;
  justify-content: flex-end;
  background-color: #7793f4;
  padding: 30px 0 30px 0;
}
.header__user {
  color: #f9f9f9;
  font-size: 1.2em;
  margin-right: 10px;
}
.header__button {
  border-color: #f9f9f9;
  font-size: 1.2em;
  margin: 0px 20px 0 0;
  padding: 10px 15px;
  border-radius: 10px;
  text-decoration: none;
  color: #f9f9f9;
  border: 2px solid;
}
.header__menu {
  display: flex;
  align-items: center;
  z-index: 3;
}
@media (max-width: 375px) {
  .header__button {
    font-size: 1em;
  }
  .header__user {
    font-size: 1em;
  }
}
.header__gallery{
  display: none;
}
.icon-menu{
  display: none;
}
@media (max-width: 768px) {
  .header{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    background-color: #f9f9f9;
  }
  .header__button {
    margin-top: 20px;
  }
  .header__menu {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: #7793f4;
    transform: translateY(-100%);
    transition-duration: 1s;
    
  }
  .header__menu.active {
    transform: translateY(-10%);
  }
  .header__menu {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
.header__gallery{
  display: block;
    position: absolute;
    top: 18px;
    left: 30px;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
}
.header__gallery>img{
  height: auto;
}
  .icon-menu {
    display: block;
    position: absolute;
    top: 18px;
    right: 30px;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;
  }
  .icon-menu>img{
    height: auto;
  }
  .menu__body {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    background-color: #595959;
    padding-top: 150px;
    transform: translateY(-100%);
    transition-duration: 1s;
    z-index: 1;
  }
 
}

