img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.image-grid.filter{
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
}
.image-grid {
	display: grid;
	gap: 0.5rem;
	grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
}

@media (max-width: 425px){
    .image-grid.filter{
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    }
}

@media (max-width: 1024px){
    .image-grid {
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
}
.unlike{
    position: relative;
    opacity: 0.7;
}


.gallery__card-unlike{
    position: relative; 
    background-color: red;
}
.like{
    opacity: 0.7;  
}
.gallery__card{
    position: relative;
    transition: 1s;
}
.gallery__card:hover{
    background-color: #595959;
}

.gallery__like{
    font-size:150px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 1;
    color : red;
    opacity: 0;
    cursor: pointer;
   transition: 1s;
}
.gallery__like:hover{
    opacity: 1;
}
form{
    margin: 30px auto 30px;
    text-align: center;
  }
  .gallery__label input{
    height: 0;
    width: 0;
    opacity: 0;
  }
  .gallery__label{
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid #7793f4;
    border-radius: 50%;
    margin: 10px auto;
    line-height: 25px;
    color:#7793f4;
    font-weight: bold;
    font-size: 30px;
    cursor: pointer;
  }
  label:hover{
    background: #7793f4;
    color: white;
    transition-duration: 1s;
  }
  
.gallery__btn button{
    border-color: antiquewhite;
    font-size: 1.2em;
    color: #f9f9f9;
    border: 2px solid;
    background-color: #7793f4;
    border-radius: 10px;
    padding: 15px;
    cursor: pointer;
}
.gallery__btn{
    display: flex;
    justify-content: center;
    margin: 30px ;
    
}
.gallery__filter{
    display: none;
}
@media (max-width: 768px){
    .gallery__filter{
        display: block;
        width: 30px;
        margin: 20px 20px 20px 90%;
        
    }
    form{
        margin: 0;

      }
    .gallery__label{
        position: fixed;
    bottom: 4%;
    left: 50%;
    transform: translate(-50%);
    z-index: 3;
    border: none;
    }
    .gallery__label > span{
     font-size: 50px;
     background-color: #7793f4;
     border-radius: 50%;
     padding:10px 10px 18px 10px;
     color: #f9f9f9;
     position: fixed;
    bottom: 0px;
    left: 50%;
    transform: translate(-50%);
    z-index: 3;
    }
    .gallery__btn{ 
        margin: 30px 0 85px 0;
    }
}
