.sing{
    height: 100vh;
    background-color: #7793f4;
}

.sing__button {
    border-color: #f9f9f9;
    font-size: 1.2em;
    color: #7793f4;
    border: 2px solid;
    background-color: #f9f9f9;
    border-radius: 25px;
    padding: 10px 15px;
    margin: 10px auto;
    text-decoration: none;
    width: 250px;
    display: flex;
    justify-content: center;
}
.sing__button-2{
    
    font-size: 1.2em;
    color: #f9f9f9;
    border: 2px solid #e63a2e;
    background-color: #e63a2e;
    border-radius: 25px;
    padding: 10px 15px;
    margin: 10px auto;
    text-decoration: none;
    width: 250px;
    display: flex;
    justify-content: center;
}
.wrapper:first-child{
    margin-top: 50px;
}

.sing__container{
   
    text-align: center;
    margin: 0 auto;
}

.sing__container h1{
margin: -15% 0 0 0 ;
font-size: 1.5em;
}
.sing__container img{
    width: 300px;
}
.wrapper{
    position: relative;
    width: 300px;
    margin: 0 auto;
}
.sing__arrow{
    position: absolute;
    right: 50px;
    top: 15px;
    color: #7793f4;
}
.sing__arrow-2{
    position: absolute;
    right: 50px;
    top: 15px;
    color: #f9f9f9;
}