.modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.698);
    z-index: 2;
    display: flex;
  }
  
  .modal__img{
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: auto 0 auto auto;
    box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
    
    z-index: 2;
  }
  .modal__info{
    display: block;
    max-width: 50%;
    max-height: 80%;
    height: 100%;
    margin: auto auto auto 0;
    box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
    background: white;
    z-index: 2;
  }
  .modal__text{
      margin: 25px 10px;
  }
.modal__like{
    color: red;
    padding-left: 5px;
    transition: 0.5s;
}
.modal__like:hover{
    font-size: 1.1em;
}
@media (max-width: 767px){
    
    .modal{
        flex-direction: column;
      }
      .modal__info{
        display: block;
        max-width: 85%;
        max-height: 70%;
        height: 10%;
        width: 85%;
        margin: 0 auto auto auto;
      }
      .modal__img{
        display: block;
        max-width: 85%;
        max-height: 70%;
        margin: auto auto 0 auto;
      }
}
