.login{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #7793f4;
    color: #f9f9f9;}
.login__form{
    width: 320px;
    padding: 10px;
    letter-spacing: 1px;
    margin-top: -25%;
}
.login__input{
    width: 100%;
    padding: 0 0 10px 0;
    border: none;
    border-bottom: 1px solid #f9f9f9;
    outline: none;
    background: transparent; 
    transition: 0.5s;
    margin-bottom: 30px;
    color: #f9f9f9;
}
.login__input::placeholder{
    color: #f9f9f9;
}
.login__title{
    margin: 0;
    
}
.login-input{
    border-bottom: none;
}
.login__button{
    border-color: #f9f9f9;
    font-size: 1.2em;
    color: #7793f4;
    border: 2px solid;
    background-color: #f9f9f9;
    border-radius: 25px;
    padding: 10px 15px;
    margin: 10px auto;
    text-decoration: none;
    width: 250px;
}
.login__btn{
    display: flex; 
    justify-content: center;
    flex-direction: column;
    position: relative;
}
.login__arrow{
        position: absolute;
        right: 50px;
        top: 23px;
        color:#7793f4
}
.login__text{
    font-size: 0.7em;
    margin: 0 0 30px 0;
    
}
.login__password{
    position: relative;
}
.login__eye{
    position: absolute;
    right: 0;
    top: 30px;
    z-index: 1;
}
.login__password img{
    width: 16px;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
 
  -webkit-text-fill-color: #f9f9f9;
  -webkit-box-shadow: 0 0 0px 1000px #7793f4 inset;
  transition: background-color 5000s ease-in-out 0s;
}