.register{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #7793f4;
    color: #f9f9f9;
}
.register__form{
    width: 320px;
    padding: 10px;
    letter-spacing: 1px;
    margin-top: -25%;
}
.register__input{
    width: 100%;
    padding: 0 0 10px 0;
    border: none;
    border-bottom: 1px solid #f9f9f9;
    outline: none;
    background: transparent; 
    transition: 0.5s;
    margin-bottom: 30px;
    color: #f9f9f9;
}
.register__input::placeholder{
    color: #f9f9f9;
}
.register__title{margin: 0;}
.register__button{
    border-color: #f9f9f9;
    font-size: 1.2em;
    color: #7793f4;
    border: 2px solid;
    background-color: #f9f9f9;
    border-radius: 25px;
    padding: 10px 15px;
    margin: 10px auto;
    text-decoration: none;
    width: 250px;
}
.register__btn{
    display: flex; 
    justify-content: center;
    flex-direction: column;
    position: relative;
}
.register__arrow{
    position: absolute;
        right: 50px;
        top: 23px;
        color:#7793f4
}
.register__eye{
    position: absolute;
    right: 0;
    top: 30px;
    z-index: 1;
}
.register__password img{
    width: 16px;
}
.register__password{
    position: relative;
}
.register__text{
    font-size: 0.7em;
    margin: 0 0 30px 0;
}